.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left:0;
    background-color: rgba(255,255,255,0.4);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
    color: #fffffa;
    flex:1
}

.Toolbar nav {
    height: 100%;
    
}

.Logo {
    height: 80%
}

@media (max-width: 499px){
    .DesktopOnly {
        display: none
    }
}