.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: rgba(0, 35, 67, 1);
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out
}
.Open {
    transform: translateX(0)
}

.Close {
    transform: translateX(-100%)
}
@media (min-width: 500px){
    .SideDrawer {
        display: none
    }
}

.Logo {
    height: 11%;
    margin-bottom:32px;
}

